#search_spinner {
    display: None;
    position: absolute;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100px;
    width: 100px;
  }
  
  /* Spinner */
  
  .circle {
    fill: #000;
  }
  
  .circle1 {
    fill: #000;
  }
  
  .circle2 {
    fill: #222;
  }
  
  .circle3 {
    fill: #444;
  }
  
  .circle4 {
    fill: #666;
  }
  
  .circle5 {
    fill: #888;
  }
  
  .circle6 {
    fill: #bbb;
  }
  
  .circle7 {
    fill: #ddd;
  }
  
  .circle8 {
    fill: #fff;
  }
  
  .spinner {
    display: none;
    -webkit-animation: rotation 0.75s steps(8) infinite;
            animation: rotation 0.75s steps(8) infinite;
  }
  
  @-webkit-keyframes rotation {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes rotation {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  /* End Spinner */


